    [PageHeader.name]: PageHeader,
    [Button.name]: Button,
    [Drawer.name]: Drawer,
    [Input.name]: Input,
    [Icon.name]: Icon,
    [Tooltip.name]: Tooltip
  },
  data() {
    return {
      title: 'Luck Wheel',
      subTitle: 'This is a subtitle.',
      visible: false,
      showEmpty: false,
      prizeList: [
        {
          txt: '金豆1',
          img: require('@/assets/img/lucky-whell/bean_one.png')
