    // 开始转动
    go() {
      if (this.valve) {
        return
      }
      this.valve = true

      // 模拟随机数，这里可以请求后台获取中将信息
      const index = Math.floor(Math.random() * this.prizeList.length)
      this.winningIndex = index

      // 转动转盘
      this.$refs.bigWheel.rotate(index)
    },
    // 转盘转完事件
    over(prizeInfo) {
      alert(`恭喜获得${prizeInfo.txt}`)
      this.valve = false
    },
    afterVisibleChange(val) {
      console.log('visible', val);
    },
    showDrawer() {
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    },
    handleRandomItem() {
      this.randomItem()
    },
    handleClearCache() {

    },
